import React from "react";
import {
  Group,
  ScreenSpinner,
  Button,
  Panel,
  PanelHeader,
  Gradient,
  Avatar,
  Title,
  Text,
  Header,
  SimpleCell,
} from "@vkontakte/vkui";
import connect from "@vkontakte/vk-bridge";
import "./main.css";
import axios from "axios";
const numberWithSpace = (number) =>
  number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ");
let fix = (n) => numberWithSpace(parseFloat(n).toFixed(0));
let ava = "";
let user_id = 1;
let balance = 0;
let bonusbalance = 0;
let user_data_name = "";
let user_data_name2 = "";
let klan = "";
let nick = "";

let totalwin = 0;
let weekwin = 0;
let todaywin = 0;
let totalbuy = 0;
let todaybuy = 0;

let placetotal = 0;
let placeweek = 0;
let placetoday = 0;
let placetodaybuy = 0;

async function get() {
  this.setState({ popout: <ScreenSpinner /> });

  let user_info = await connect.sendPromise("VKWebAppGetUserInfo");
  user_data_name = user_info.first_name;
  user_data_name2 = user_info.last_name;
  ava = user_info.photo_max_orig;
  user_id = Number(user_info.id);
  let getbal = await axios({
    method: "POST",
    url: `https://lucky-coin.ru/user/balance`,
    headers: {
      "Content-Type": "application/json",
    },
    data: {
      id: Number(user_id),
    },
  });
  connect.sendPromise("VKWebAppShowNativeAds", { ad_format: "reward" });
  balance = getbal.data.response.balance;
  bonusbalance = getbal.data.response.bonusbalance;
  klan = getbal.data.response.clan;
  nick = getbal.data.response.name;
  totalwin = getbal.data.response.totalWin;
  weekwin = getbal.data.response.weekWin;
  todaywin = getbal.data.response.todayWin;
  totalbuy = getbal.data.response.totalbuy;
  todaybuy = getbal.data.response.todaybuy;
  placetotal = getbal.data.response.placetotal;
  placeweek = getbal.data.response.placeweek;
  placetoday = getbal.data.response.placetoday;
  placetodaybuy = getbal.data.response.placetodaybuy;

  this.setState({ popout: null });
}
get();
